import React from "react";
import { NavLink, useHistory } from "react-router-dom";
import Logo from '../logo.svg'
import styled from 'styled-components'
import { Button } from 'antd';
import  { useStores} from '../stores'
import {observer} from 'mobx-react';
import { message } from "antd";

const Header = styled.header`
background-color:#02101f;
display:flex;
padding:10px 100px;
align-items:center;  //垂直
// justify-content:center; //水平居中
`;
const Img = styled.img`
height:30px;
width:30px
`

const StyleLink = styled(NavLink)`  //按原来的link标签传递进来，返回一个新的StyleLink组件。
color:#fff;
margin-left:30px;
&.active {border-bottom:1px solid #fff}
`

const Login = styled.div`
margin-left:auto;
// &.btn{margin-left: 20px;}

`;
const StyledButton = styled(Button)`
margin-left: 10px;
`;
const Span = styled.span`
color:#fff;
`


const Component = observer(() => {   //Component表示导出的是一个组件
    const {UserStore,AuthStore}  = useStores()
    const history = useHistory()   //不加{}

    const handleLogin = () => {
        history.push('/login')  //跳转到登录页面
    }
    
    const handleRegister = () => {
        history.push('/register')
    }
  
    const handleLogout = () => {
        AuthStore.logout()
        //history.push('/register')
        message.info('用户已注销')
    }




    return (
        <Header>
            <Img src={Logo} />
            <nav>
                <StyleLink to='/' activeClassName="active" exact>首页</StyleLink>
                <StyleLink to='/history' activeClassName="active">上传历史</StyleLink>
                <StyleLink to='/about' activeClassName="active">关于</StyleLink>
            </nav>
            <Login>
                {
            UserStore.currentUser ? <>    
           <Span> {UserStore.currentUser.attributes.username} </Span> <StyledButton type="primary" onClick={handleLogout}>注销</StyledButton>
          </> : <>
          <StyledButton type="primary" onClick={handleLogin}>登录</StyledButton>
          <StyledButton type="primary" onClick={handleRegister}>注册</StyledButton>
          </>
                }
            </Login>
        </Header>
    );
})
export default Component;