import {observable,action,makeObservable} from 'mobx';
import {Uploader} from '../models';
import {message} from 'antd'

class ImageStore {
    constructor() {
        makeObservable(this)
    }

 @observable filename = '';
 @observable file = '';  //通过本地input加载的文件
 @observable isUpoading = false;  //上传状态
 @observable serverFile = null  //服务器返回的线上地址

 @action setFilename(newFilename){
    this.filename =  newFilename
 }
 @action setFile(newFile){
     this.file = newFile
 }
 @action  upload() {   //上传
    this.isUpoading = true;
    this.serverFile = null;   //上传时先把地址清空。
   return  new Promise((resolve,reject) => {
    Uploader.add(this.file,this.filename)
    .then(serverFile => {
        this.serverFile = serverFile;
        resolve(serverFile)
        message.success('上传成功')
    }).catch(err => {
        console.log('上传失败')
        message.error('上传失败')
        reject(err)
    }).finally(() => {
     this.isUpoading = false;   //最后，关闭上传状态
    })
   }) 

 }
 @action reset(){
     this.isUpoading = false;
     this.serverFile = null;
 }


}
export default new ImageStore();