import {Auth} from '../models';
import {observable,action,makeObservable} from 'mobx'; 



class UserStore {

    constructor() {
        makeObservable(this)  //表示userStore是可监控的
    }

    @observable currentUser = null;   //当前用户信息
    @action pullUser() {   //拉取用户信息
        this.currentUser = Auth.getCurrentUser()  //获取当前用户的信息
    }

    @action resetUser(){
        this.currentUser = null;   //清空用户信息
    }
}

export  default new UserStore();